@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,300;1,400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* border: 1px solid red; */
}
body {
  font-family: "Montserrat";
  color: rgba(240, 248, 255, 0.87);
}
.app-wrapper {
  background-image: url(/weather-app/static/media/jerry-zhang-SJGiS1JzUCc-unsplash.eff13f74.webp);
  background-position: 0 0;
  background-repeat: no-repeat;
  background-size: cover;
  transition: 0.4s ease-out;
}

main {
  min-height: 100vh;
  width: 100%;
  padding: 0 6%;
  background-image: linear-gradient(
    to bottom,
    rgba(0, 0, 0, 0.2),
    rgba(0, 0, 0, 0.75)
  );
  padding: 2rem;
}
/* Search input */
.search-box {
  width: 49rem;
  margin: 0 auto;
  display: flex;
  margin-bottom: 5rem;
}

.search-box button {
  border-radius: 0 0 1rem 0;
  margin-top: -2rem;
  font-family: "Montserrat" !important;
  font-size: 1rem;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  transition: 0.4s ease;
  width: 9rem;
  border: none;
  outline: none;
  color: rgba(240, 248, 255, 0.87);
  background-color: rgba(70, 70, 70, 0.2);
  text-transform: uppercase;
  cursor: pointer;
}
.search-box button:hover {
  background-color: rgba(42, 42, 42, 0.2);
}

.search-box .search-input:focus {
  background-color: rgba(255, 255, 255, 0.75);
}
.validation {
  text-align: center;
  color: rgba(236, 189, 184, 0.897);
  font-size: 1.1rem;
  position: absolute;
  top: 4rem;
  left: 43%;
}
/* Current weather */
.location-box {
  margin-bottom: 4rem;
}
.location-box .location {
  font-size: 3.5rem;
  font-weight: 700;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}
.date {
  font-size: 1.2rem;
  font-weight: 400;
  text-shadow: 1px 1px rgba(50, 50, 70, 0.5);
}

.main-wrapper {
  display: flex;
  width: 100%;
  margin-bottom: 5rem;
  flex-wrap: wrap;
  justify-content: space-between;
}

.weather-main {
  display: flex;
  justify-content: start;
  width: 50%;
  flex-wrap: wrap;
}
.icon-main {
  width: 7rem;
  height: 7rem;
}

.temp-main {
  font-size: 6rem;
  font-weight: 800;
  text-shadow: 3px 4px rgba(50, 50, 70, 0.5);
  text-align: center;
}
.weather-main {
  text-shadow: 3px 3px rgba(50, 50, 70, 0.5);
  font-size: 2.5rem;
  font-weight: 600;
}
.descr-main {
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
  text-align: center;
  font-weight: 300;
  font-size: 1.4rem;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
  background-color: rgba(255, 255, 255, 0.123);
  border-radius: 1rem;
  width: 38rem;
  padding: 1rem 2.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

/* Forecast */
.data-wrapper {
  display: flex;
  width: 100%;
  overflow: auto;
  margin-top: 2rem;
}

/* Error message and loader */
.error {
  font-size: 1.6rem;
  font-weight: 400;
  text-align: center;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 0 auto;
  margin-bottom: 2rem;
  padding: 1rem 1.5rem;
  color: rgba(236, 189, 184, 0.87);
  background-color: rgba(255, 108, 108, 0.308);
  box-shadow: 3px 6px rgba(93, 46, 46, 0.596);
  border-radius: 1rem;
}

.start-message {
  font-size: 3rem;
  font-weight: 600;
  width: 70%;
  margin: 0 auto;
  text-align: center;
  padding: 1rem 2rem;
  text-shadow: 2px 2px rgba(50, 50, 70, 0.5);
}

.loader {
  height: 6rem;
  margin: 0 auto;
  display: block;
  margin-top: 2rem;
  color: rgba(255, 255, 255, 0.123);
}

/* Media queries */
@media only screen and (max-width: 992px) and (min-device-width: 601px) {
  .search-box button {
    width: 6rem;
  }
  .search-box {
    width: 41rem;
  }
  .descr-main {
    margin-top: 2rem;
    padding: 2rem 2.5rem;
  }
  .start-message {
    width: 90%;
    font-size: 2.7rem;
  }
}
@media only screen and (max-width: 600px) {
  .search-box button {
    width: 5rem;
    font-size: 0.8rem;
  }
  .search-box {
    width: 18rem;
  }
  .error {
    font-size: 1.3rem;
  }
  .icon {
    width: 3rem;
  }
  .location-box .location {
    font-size: 2.5rem;
  }
  .icon-main {
    width: 5rem;
    height: 5rem;
  }
  .temp-main {
    font-size: 4.5rem;
  }
  .weather-main {
    font-size: 2rem;
  }
  .descr-main {
    margin-top: 1.7rem;
    font-size: 0.9rem;
    padding: 1rem;
  }
  .start-message {
    width: 100%;
    font-size: 1.7rem;
  }
  .validation {
    font-size: 1rem;
    left: 21%;
  }
}

.search-input {
  display: block;
  width: 40rem;
  margin: 0 auto;
  padding: 1rem;
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  background: none;
  border: none;
  outline: none;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0 0 0 1rem;
  margin-top: -2rem;
  font-family: "Montserrat" !important;
  font-size: 1rem;
  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  transition: 0.4s ease;
}
@media only screen and (max-width: 600px) {
  .search-input {
    width: 13rem;
  }
}
@media only screen and (max-width: 992px) and (min-device-width: 601px) {
  .search-input {
    width: 35rem;
  }
}

